import React, { useContext, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';

import Button from '../Components/Button';
import Input from '../Components/Input';
import { MainContext } from '../Context/MainContext';
import '../style/joinPage.css';
function JoinPage() {
  const [isJoin, setIsJoin] = useState("main");
  const [name, setName] = useState('');
  const [roomId, setRoomId] = useState('');
  const { setRoomData } = useContext(MainContext);
  const generateRoomId = () => {
    return uuidv4();
  }

  const navigation = useNavigate();
  useEffect(() => {
    //  setRoomId(generateRoomId());
  }, []);


  const onCreate = () => {
    if (name === "") {
      alert("Please Enter Name");
    } else {
      const data = {
        name: name,
        roomId: roomId
      };
      setRoomData(data);
      navigation("/room");
    }
  }

  const joinRoom = () => {
    if (roomId == "" && name == "") {
      alert("Please Enter Name");

    } else {
      const data = {
        name: name,
        roomId: roomId
      };
      setRoomData(data);
      navigation("/room");
    }
  }



  return (
    <div className='container'>
      {
        isJoin === "create" ?
          <div className='createForm'>
            <div className='header'>
              <button onClick={() => { setIsJoin("main") }}>&times;</button>
              <h3>Create Room</h3>
            </div>
            <div>
              <Input hint="Please Enter Your Name" readOnly={false} onChange={(e) => { setName(e.target.value) }} value={name} />
              <Input hint={"please Enter Room Id"} onChange={(e) => { }} readOnly={true} value={roomId} />
              <Button title={"Create Room"} onPress={() => { onCreate() }} bgColor="#BB8FCE" textColor="#fff" />
            </div>
          </div>
          :
          isJoin === "join" ?
            <div className='joinForm'>
              <div className='header'>
                <button onClick={() => { setIsJoin("main") }}>&times;</button>
                <h3>Join Room</h3>
              </div>
              <div>
                <Input hint="Please Enter Your Name" onChange={(e) => { setName(e.target.value) }} value={name} />
                <Input hint={"please Enter Room Id"} onChange={(e) => { setRoomId(e.target.value); }} value={roomId} />
                <Button title={"Join Room"} onPress={() => { joinRoom(); }} bgColor="#BB8FCE" textColor="#fff" />
              </div>
            </div> : <div className="joinCard">
              <h3>Create And Join Room</h3>
              <Button title={"Create Room"} onPress={() => { setRoomId(generateRoomId()); setIsJoin("create") }} bgColor="#BB8FCE" textColor="#fff" />
              <Button title={"Join Room"} onPress={() => { setIsJoin("join") }} />
            </div>
      }
    </div>
  )
}

export default JoinPage