import React from 'react'

function Input({ hint, value, onChange, readOnly }) {
    return (
        <input type={"text"} style={{
            width: "80%",
            border: "1px solid #111",
            outline: "none",
            height: "5vh",
            marginBlock: 10,
            borderRadius: 5
        }}
            readOnly={readOnly}
            placeholder={hint}
            value={value}
            onChange={onChange}
        />
    )
}

export default Input